import classes from "./country.css"

import React, { Component } from 'react'
import CardGroup from 'react-bootstrap/CardGroup'
import Casebox from "../caseboxes/casebox"
import { Pie } from 'react-chartjs-2';
import Spinner from "../Spinner/Spinner"



class Country extends Component {
    state = {
        country: 'India',
        cases:{
            total:0,
            confirmed:0,
            recovered:0,
            deaths:0

        },
        loading:false
      
    }
  
    componentDidMount(){

        this.setState({...this.state,loading:true})
        fetch(`https://disease.sh/v3/covid-19/countries/${this.state.country}`,{
            method:'GET'
        })
        .then(response=>response.json())
        .then(jsondata=>{
            this.setState({cases:{
                ...this.state.cases,
                total:jsondata.cases,
                confirmed:jsondata.active,
                recovered:jsondata.recovered,
                deaths:jsondata.deaths
            },
            loading:false
        })
        })
        .catch(error=>{
            console.log(error)
        })
    }
  

    setCountry=(event)=>{
        fetch(`https://disease.sh/v3/covid-19/countries/${event.target.value}`,{
            method:'GET'
        })
        .then(response=>response.json())
        .then(jsondata=>{
            this.setState({
                country:event.target.value,
                ...this.state.cases,
                cases:{
                    total:jsondata.cases,
                    confirmed:jsondata.active,
                    recovered:jsondata.recovered,
                    deaths:jsondata.deaths

                }
               
            })
            
         
        })
        
        .catch(error=>{
            console.log(error)
        })

      

    }
    

    countries = ['India', 'Germany', 'USA', 'UK', 'France', 'Italy', 'Pakistan', 'Brazil', 'South Africa',
        'Bangladesh', 'UAE', 'Kuwait', 'Oman', 'Qatar', 'Japan', 'China', 'Russia', 'Germany', 'Poland', 'Portugal',
        'Argentina', 'Chile', 'Vietnam', 'Laos', 'Singapore', 'Malaysia', 'Thailand', 'Iceland', 'Canada', 'Mexico',
        'Egypt', 'Tanzania', 'Yemen', 'South Korea', 'Australia', 'New Zealand', 'Saudi Arabia', 'Iran', 'Iraq',
        'Israel', 'Lebanon', 'Turkey', 'Switzerland', 'Norway', 'Sweden', 'Netherlands', 'Denmark']
    render() {
       
        return (
            <div>
                {this.state.loading ? <Spinner/> :(
                <div className={classes.countrydropdown} lg={12} sm={12} md={12}>
                    <select name='countries' className={classes.dropdown} 
                      onChange={(event)=>this.setCountry(event)}>
                        {this.countries.map((country,index) => {
                            return (
                            <option key={index}  value={country}>{country}</option>
                            )
                            })}
                    </select>
                    <CardGroup style={{ display: 'flex', justifyContent: "space-evenly" }}>
                        {Object.keys(this.state.cases).map(item => {
                            return (<Casebox key={item} type={item} number={this.state.cases[item]} />
                                )
                                })}

                    </CardGroup>
                    


                </div>
                )}
              
                
            </div>
        )
    }
}
export default Country
