import React, {useState,useEffect } from "react"
import classes from "./Cases.css"
import Casebox from "../caseboxes/casebox"
import CardGroup from 'react-bootstrap/CardGroup'

const Cases=()=>{
    const [cases,Setcases]=useState({
        total:0,
        confirmed:0,
        recovered:0,
        deaths:0

    })
    useEffect(()=>{
        
        fetch("https://disease.sh/v3/covid-19/all", {
            method: 'GET'
        })
        .then(response=>response.json())
        .then(jsondata=>{
            Setcases({total:jsondata.cases,
                confirmed: jsondata.active,
                recovered: jsondata.recovered,
                deaths: jsondata.deaths,})
        })
        .catch(error=>{
            console.log(error)
        })
  
        

    },[cases])
    return (
        <div className={classes.Cases}>
          
            <CardGroup style={{ display: 'flex', justifyContent: "space-evenly" }}>
                {Object.keys(cases).map(item => {
                    return (<Casebox key={item} type={item} number={cases[item]} />
                    )
                })}
            </CardGroup> 
          
          


        </div>

    )
    
}



export default Cases