import React from 'react'
import Card from 'react-bootstrap/Card'
import classes from "./casebox.css"


const Casebox = (props) => {
    let styleclass=null
    let titleclass=null
    switch(props.type){
        case 'total':
            styleclass=classes.casebox_black
            titleclass={
                'color':'#2e3134'
            }
            break
        case 'confirmed':
            styleclass=classes.casebox_blue
            titleclass={
                'color':'#3343ec'
            }
            break
        case 'recovered':
            styleclass=classes.casebox_green
            titleclass={
                'color':'#04ab65'
            }
            break
        case 'deaths':
            styleclass=classes.casebox_red
            titleclass={
                'color': '#ef4b1f'
            }
            break
        default:
            break
        
    }
    
    return (
        <Card className={styleclass} md={6}>
            <Card.Body>
                <Card.Title style={titleclass}>{props.type}</Card.Title>
                <Card.Subtitle style={titleclass}>{props.number}</Card.Subtitle>
            </Card.Body>

        </Card>



    )
}

export default Casebox
