import React from "react"
import classes from "./header.css"
import Row from 'react-bootstrap/Row'

const Header=()=>{
    return(
        <Row className={classes.maindiv} sm={12} lg={12} md={6}>
            <span className={classes.heading}>CoronaTrackerApp</span>
            <span className={classes.mytime}>{new Date().toDateString()}</span>
        </Row>
    )
}
export default Header