export const PaginationOptions = (country) => {
    const countries = ['India', 'Israel', 'Japan', 'USA', 'Canada', 'Australia', 'China', 'Germany',
        'France', 'New Zealand', 'Russia', 'United Kingdom', 'Hong-Kong', 'Belgium', 'Italy',
        'Indonesia', 'Lithuania', 'Mexico', 'Netherlands', 'Nigeria', 'Portugal', 'Singapore',
        'South Africa', 'South Korea', 'Thailand', 'Switzerland'
    ]
    const countrycodes = ['in', 'il', 'jp', 'us', 'ca', 'au', 'cn', 'de', 'fr', 'nz', 'ru', 'gb', 'hk',
        'be', 'it', 'id', 'lt', 'mx', 'nl', 'ng', 'pt', 'sg', 'za', 'kr', 'th', 'ch'
    ]
    const code = countrycodes[countries.indexOf(country)]
    return code

}
export const countries = ['India', 'Israel', 'Japan', 'USA', 'Canada', 'Australia', 'China', 'Germany',
    'France', 'New Zealand', 'Russia', 'United Kingdom', 'Hong-Kong', 'Belgium', 'Italy',
    'Indonesia', 'Lithuania', 'Mexico', 'Netherlands', 'Nigeria', 'Portugal', 'Singapore',
    'South Africa', 'South Korea', 'Thailand', 'Switzerland'
]
