import React, { Component } from 'react'
import classes from "./News.css"
import {PaginationOptions,countries} from "../../pageoptions"
import Spinner from "../Spinner/Spinner"

class News extends Component {
    state={
        totalArticles:[],
        articles:[],
        articlenos:0,
        tabclicked:null,
        loading:false
    }

    componentDidMount(){
        this.setState({
            ...this.state,
            loading:true
        })
        const proxyUrl = "https://cors-anywhere.herokuapp.com/"
        fetch(`${proxyUrl}https://newsapi.org/v2/top-headlines/?country=in&category=health&apiKey=12ccf169756f48a5bc41bfd96477430a`,{
            method:'GET'
        })
        .then(response=>response.json())
        .then(jsondata=>{
            this.setState({articles:jsondata.articles.slice(0,1),articlenos:jsondata.articles.length,totalArticles:jsondata.articles,loading:false})
        })
    }
    clickedHandler=(item)=>{
        const firstpage=item-1
        const lastpage=item
        this.setState({articles:this.state.totalArticles.slice(firstpage,lastpage),tabclicked:item,loading:false})

    }

    selectcountryHandler=(country)=>{
        this.setState({...this.state,loading:true})
        const code=PaginationOptions(country)
        const proxyUrl = "https://cors-anywhere.herokuapp.com/"
        fetch(`${proxyUrl}https://newsapi.org/v2/top-headlines/?country=${code}&category=health&apiKey=12ccf169756f48a5bc41bfd96477430a`,{
            method:'GET'
        })
        .then(response=>response.json())
        .then(jsondata=>{
            this.setState({articles:jsondata.articles.slice(0,1),totalArticles:jsondata.articles,loading:false,articlenos:jsondata.articles.length})
        })
        

    }
    

   

    // api key 12ccf169756f48a5bc41bfd96477430a
    // api password newsapi123

    render() {
        const renderpagearray=[]
        let toberendered=this.state.articlenos
        if(this.state.articlenos > 10){
            toberendered=10

        }
        for(let i=1;i <= toberendered; i++){
            renderpagearray.push(i)
        }

        const renderpages=renderpagearray.map((item,index)=><li key={index} className={this.state.tabclicked ? 'li_active' :classes.li} 
        onClick={()=>this.clickedHandler(item)}>{item}</li>)
        
        const renderarticles=this.state.articles.map((item,index)=>{
            return (
                <div className={classes.News} key={index}>
                <p style={{fontWeight:'bold'}}>{item.title}</p>
                <p>{item.description}</p>
            </div>

            )
        })
     

            

    
        return (
            <div style={{height:'500px' ,textAlign:'center'}}>
                <select name="" id="" className={classes.dropdown} onChange={(event)=>this.selectcountryHandler(event.target.value)}>
                    {countries.map((country,index)=>{
                        return (<option key={index} value={country}>{country}</option>)
                    })}
                </select>
                 <div xs={12} md={12} lg={12}>
                     {this.state.loading ? <Spinner/> :renderarticles}
                </div>
            <div style={{textAlign:'center' ,marginTop:'50px'}}>
                <ul>
                    {renderpages}
                </ul>
                
            



            </div>
           
            </div>
           
           
        )
    }
}
export default News