import React from 'react'
import classes from "./Navbar.css"
import { NavLink } from 'react-router-dom'
import Row from 'react-bootstrap/Row'
const Navbar=()=>{
    return (
        <Row className={classes.Navbar} sm={12} lg={12} md={6}>
            <NavLink to='/' className={classes.Navbaritems}>World</NavLink>
            <NavLink to='/Country' className={classes.Navbaritems}>Country</NavLink>
            <NavLink to='/News'className={classes.Navbaritems}>News</NavLink>
            <NavLink to='/Help' className={classes.Navbaritems}>Help</NavLink>
            
        </Row>
    )
}

export default Navbar
